function yupResolver(schema) {
  var _schema = schema;
  return function (values) {
    try {
      _schema.validateSync(values, {
        abortEarly: false
      });

      return {};
    } catch (_yupError) {
      var yupError = _yupError;
      var results = {};
      yupError.inner.forEach(function (error) {
        results[error.path.replace("[", ".").replace("]", "")] = error.message;
      });
      return results;
    }
  };
}

